import { configureStore,
    combineReducers
    } from "@reduxjs/toolkit";
import userReducer from "./userRedux";
import productReducer from "./productRedux";

import { 
  persistStore,
  persistReducer,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist';
import storage from 'redux-persist/lib/storage';

const persistConfig = {
  key:'root',
  version:1,
  storage, 
}

// fijate que los nombres vienen del name del xxSlice
const rootReducer = combineReducers({
  user:userReducer,
  product:productReducer,
})

// Ejemplo para un único reducer,no será lo normal
// const persistedReducer = persistReducer(persistConfig,userReducer);

// lo normal será usar un rootReducer que usó la función combineReducers
const persistedReducer = persistReducer(persistConfig,rootReducer);

export const store = configureStore({
/*   reducer: { 
     cart: cartReducer ,
     user: persistedReducer
 }, */
//  dado que he combinado los reducer ya no sera un {}
 reducer:persistedReducer,
 middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
      },
    }),
});

export let persistor = persistStore(store)

